import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: '健康数据',
			redirect: '/views/classroomreport/index'
		}, {
			path: '/views',
			name: '视图容器',
			component: resolve => require(['@/views/views'], resolve),
			redirect: '/views/classroomreport/index',
			children: [
				{
					path: 'healthData',
					name: '健康数据模块父级路由',
					component: resolve => require(['@/views/healthData/healthData'], resolve),
					redirect: '/views/healthData/index',
					children: [
						{
							path: 'index',
							name: '健康数据',
							component: resolve => require(['@/views/healthData/index'],resolve)
						},
						{
							path: 'perinfo',
							name: '个人信息',
							component: resolve => require(['@/views/healthData/perInfo'],resolve)
						},
					]
				},
				{
					path: 'ClassroomReport',
					name: '课堂报告模块父级路由',
					component: resolve => require(['@/views/ClassroomReport/ClassroomReport'], resolve),
					redirect: '/views/classroomreport/index',
					children: [
						{
							path: 'index',
							name: '课堂报告',
							component: resolve => require(['@/views/ClassroomReport/index'],resolve)
						}
					]
				},
				{
					path: 'test',
					name: 'demo测试',
					component: resolve => require(['@/views/healthData/test'],resolve)
				},
			]
		}, {
			path: '*',
			name: '404页面',
			component: resolve => require(['@/404/404'],resolve)
		}
	]
})



